.box {
    width: 100%;
    margin-left: 2%;

    .box_item {
        width: 48%;
        height: 5rem;
        float: left;
        border: 1px solid rgb(179, 177, 177);
        box-sizing: border-box;
        position: relative;
        position: relative;

        .gowuc {
            position: absolute;
            left: 0.2rem;
            bottom: 0.3em;
        }

        .gouwuchebtn {
            position: absolute;
            right: 0.2rem;
            bottom: 0.3em;
            background-color: #ff005a;
            border-radius: 50%;
            color: white;
            float: right;
            height: 0.5rem;
            font-size: 0.5rem;
            line-height: 0.5rem;
            text-align: center;
            z-index: 99;
        }


        .showitem {
            width: 100%;
            height: 5rem;
            float: left;
            border: 1px solid rgb(179, 177, 177);
            box-sizing: border-box;
            position: relative;

            .style {
                background-color: #ff005a;
                color: white;
                font-size: 12px;
                position: absolute;
                left: 0.4rem;
                top: 2.8rem;
                // text-align: center;
                // marginTop: "-0.4rem";
                // marginBottom: "0.5rem";
                // backgroundColor: "#ffe2ec";
                // color: "ffe2ec"
            }

            p {
                line-height: 0.5rem;
            }

            i {
                width: 0.5rem;
                height: 0.5rem;
                margin-top: -0.1rem;
            }


        }

    }
}